* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: Montserrat;
  font-weight: 500;
  text-decoration: none;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 320px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

