    max-width: 1200px;
  }
}

.header {
  width: 100%;
  z-index: 998;
  background-color: #fff;
  padding: 20px;
  position: fixed;
  top: -2px;
}

.header__container {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__button {
  display: none;
}

.header__menu {
  height: 38px;
  width: 38px;
  fill: #000;
}

.header__menu--hidden {
  display: none;
}

.logo {
  width: 115px;
  height: 41.89px;
}

.navigation {
  display: none;
}

@media screen and (min-width: 768px) {
  .header {
    padding: 25px;
    top: -3px;
  }

  .header__container {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .header__button, .header__menu {
    display: none;
  }

  .navigation {
    display: flex;
  }

  .navigation__item:not(:last-child) {
    margin-right: 30px;
  }

  .navigation__link {
    color: #b9b9b9;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
    transition: color .2s cubic-bezier(.17, .67, .83, .67);
  }

  .navigation__link:is(:focus, :hover) {
    color: #ff6b09;
  }

  .header {
    justify-content: center;
    display: flex;
  }

  .header__container {
    max-width: 1400px;
  }
}

.hero {
